import React, {useState} from 'react';
import {Link, useNavigate} from 'react-router-dom';
import axios from 'axios';
import { Alert, Button, Container, Form } from 'react-bootstrap';

const SignUp = () =>{
    const [user_id,setId] = useState('');
    const [user_name,setName] = useState('');
    const [user_pw,setPassword] = useState('');
    const [passwordCheck,setPasswordCheck] = useState('');
    const [term,setTerm] = useState(false);
    const [passwordError,setPasswordError] = useState(false);
    const [termError,setTermError] = useState(false);
    const [user_gender, setGender] = useState('');
    const [user_birth, setBirth] = useState('');
    const [user_email, setEmail] = useState('');
    const [user_class, setUserClass] = useState('');

    const history = useNavigate();

    //     
    
    const onSubmit = (e) => {
        e.preventDefault();

        if(user_pw!== passwordCheck){
            return setPasswordError(true);
        }
         if(!term){
            return setTermError(true);
        }
        console.log({
            user_id,
            user_name,
            user_pw,
            passwordCheck,
            user_gender,
            user_birth,
            term
        });

        sessionStorage.setItem('user', JSON.stringify({ user_id, user_pw, user_name, user_gender, user_email }));

        if(sessionStorage.getItem("user") != null) {
           history('/');
        }
    } 

    // Coustom Hook 
    const onChangeId = (e) => {
        setId(e.target.value);
    };
    const onChangeName = (e) => {
        setName(e.target.value);
    };
    const onChangePassword = (e) => {
        setPassword(e.target.value);
    };
    const onChangePasswordChk = (e) => {
        //  password   
        setPasswordError(e.target.value !== user_pw);
        setPasswordCheck(e.target.value);
    };
    const onChangeTerm = (e) => {
        // 
        setTermError(false);
        setTerm(e.target.checked);
    };

    const onChangeGender = (e) => {
        setGender(e.target.value)
    }

    const onChangeBirth = (e) => {
        setBirth(e.target.value)
    }

    const onChangeEmail = (e) => {
        setEmail(e.target.value)
    }

    const onChangeUserClass = (e) => {
      setUserClass(e.target.value)
  }

  
    // axios 
    /*
    const onSubmit = async (e) => {
      e.preventDefault();
      //  
      // 1.      
      // 2.   .
       
      if(user_pw !== passwordCheck){
          return setPasswordError(true);
      }
      if(!term){
          return setTermError(true);
      }
      console.log({
        user_id,
        user_pw,
        passwordCheck,
        user_name,
        user_gender,
        user_birth,
        user_email,
        term
      });
      try {
          const response = await axios.post('http://localhost:8080/user/join', {
            user_id:user_id,
            user_pw,
            user_name,
            user_birth,
            user_email,
            user_class,
            user_gender
          });
    
          if (response.data.success) {
            sessionStorage.setItem('user', JSON.stringify(response.data.user));
            history('/');
          } else {
            history('/');
          }
        } catch (error) {
          console.error('Signup error:', error);
          alert(" !")
        }
  }; */

    return (
        <Container>
          <h1>Sign Up</h1>
          <Form onSubmit={onSubmit}>
            <Form.Group>
              <Form.Label htmlFor="user_id"></Form.Label>
              <Form.Control name="user_id" value={user_id} required onChange={onChangeId} />
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user_pw"></Form.Label>
              <Form.Control
                name="user_pw"
                type="password"
                value={user_pw}
                required
                onChange={onChangePassword}
              />
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user-password-check"></Form.Label>
              <Form.Control
                name="user-password-check"
                type="password"
                value={passwordCheck}
                required
                onChange={onChangePasswordChk}
              />
              {passwordError && (
                <Alert variant="danger">  .</Alert>
              )}
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user_name"></Form.Label>
              <Form.Control name="user_name" value={user_name} required onChange={onChangeName} />
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user_birth"></Form.Label>
              <Form.Control
                type="date"
                name="user_birth"
                value={user_birth}
                onChange={onChangeBirth}
                required
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user_email"></Form.Label>
              <Form.Control
                type="email"
                name="user_email"
                value={user_email}
                onChange={onChangeEmail}
                required
              ></Form.Control>
              </Form.Group>
              <Form.Group>
              <Form.Label htmlFor="user_class"></Form.Label>
              <Form.Control
                type="text"
                name="user_class"
                value={user_class}
                onChange={onChangeUserClass}
                required
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="user_gender"></Form.Label>
              <Form.Control
                as="select"
                name="user_gender"
                value={user_gender}
                onChange={onChangeGender}
              >
                <option value="male"></option>
                <option value="female"></option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Check
                type="checkbox"
                name="user-term"
                value={term}
                onChange={onChangeTerm}
                label=" ?"
              />
              {termError && (
                <Alert variant="danger">  .</Alert>
              )}
            </Form.Group>
            <Button type="primary" htmltype="submit">
              
            </Button>
          </Form>
          <div style={{ marginTop: 10 }}>
            <Link to="/">
              <Button> </Button>
            </Link>
          </div>
        </Container>
      );
    };

export default SignUp;