import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import logo from "../assets/logo.png";
import cat from "../assets/imgs/cat.jpg";
import fox from "../assets/imgs/fox.jpg";
import pig from "../assets/imgs/pig.jpg";

const DefaultBody = () => {
  const settings = {
    dots: true, //  dot 
    infinite: true, //  
    speed: 5000, // 3  
    autoplay: true, //  
    autoplaySpeed: 5000, // 3  
    slidesToShow: 1, //   
    slidesToScroll: 1, //   
    centerMode: true, //   
    centerPadding: 0, //   0 
  };
  const imgStyle = {
    width: "50%",
    height: "auto",
    textAlign: "center",
    margin: "0 auto",
    display: "block",
  };
  return (
    <>
      <div style={{ margin: "100px 0 20px", textAlign: "center" }}>
        <h1 style={{ color: "deepskyblue" }}> </h1>
        <h1 style={{ color: "dodgerblue", margin: "50px 0 50px" }}>
           
        </h1>
        <h1 style={{ color: "blue", margin: "50px 0" }}> </h1>
      </div>

      <div style={{ margin: "0 0 50px" }}>
        <img src={logo} alt="logo" style={imgStyle}></img>
      </div>

      <Slider {...settings}>
        <div>
          <img src={cat} alt="" style={imgStyle} />
        </div>
        <div>
          <img src={fox} alt="" style={imgStyle} />
        </div>
        <div>
          <img src={pig} alt="" style={imgStyle} />
        </div>
      </Slider>
    </>
  );
};

export default DefaultBody;